
.about_card{
  overflow: hidden;
  border: 1px solid #eee;
  border-radius: 8px;
  box-shadow: 0 3px 10px rgb(0,0,0,.2);
  padding: 1rem;
  width: 95%;
  height: 150px;
  display:flex;
  margin: auto;
}
.about_card .about_card_icon{
    width:30%;
    height: 60%;
    font-size: 100px;
    position: relative;
    top: -15px;
    left: -10px;
}
.about_card .about_card__body{
    text-align: left;
    width: 100%;
    position: relative;
    left: 0px;
    
}
.about_card .about_card__body .about_card__title{
    text-align: left;
    font-size:larger;
}
.about_card .about_card__body .about_card__description{
    color: #787878;
    color:var(--text-color);
    font-size: var(--text-size);
}
