
.footer {
    flex-shrink: 0;
    text-align: center;
    color: white;
    background-color:rgba(2, 143, 143, 0.9);
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    }

.footer_container{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 10px;

  
}

.footer_content{
  display: flex;
  flex-direction: column;
  text-align: left;

  align-items: center;
  justify-content: center;
  width: 200px;
}

.footer_link:hover { 
    color: var(--secondary-dark);
    text-align: center;
    font-weight: bold;
    text-decoration: underline;
}

.footer-copyright{
    display: flex;
    flex-direction: row;
}
.footer-icon{
    padding-top: 17px;
    margin-right: 5px;
}
@media (max-width: 940px) {
    .footer{
        width: 100%;
    }
    .footer_container{
       display: flex;
       flex-direction: column;
   

    }
    .footer_content{
        width: 80%;
    }
    
  }