.hero {
    background-color: var(--overlay);
    color: #fff;
    height: 100vh;
    width: 120%;
    overflow-x: hidden; /* Hide horizontal scrollbar */
    z-index: 2;
    top:-100px;
  
}

.hero:before {
    content: '';
    background:url("../../../images/hero.jpg")no-repeat center center/cover;
    height: 100vh;
    width: 120%;
    position:absolute;
    top: 0px;
    left: 0px;
    z-index:-2;

}

.hero .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

.hero .content p:first-child {
    font-style: italic;
    font-size: .9rem;
}

.hero .content p:nth-child(2) {
    font-size: 1.9rem;
}

.hero .content p:nth-child(3) {
    font-size: 5rem;
    font-weight: 700;
}


.hero .content p:nth-child(4) {
    font-size: 2.2rem;
    font-style: italic;
}

.hero .button {
    margin: 1rem 0;
    width: 150px;
    height: 35px;
    background-color: #1EB2A6;
    color: #fff;
    text-align: center;
}

.hero .button:hover {
    background-color: var(--secondary-dark);
    border: 1px solid var(--secondary-dark);
}

@media screen and (max-width:940px) {
    .hero .content p:nth-child(3) {
        font-size: 2rem;
        width: 100%;
    }

    .hero .content p:nth-child(4) {
        font-size: 1.75rem;
        width: 100%;
    }


}






