@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
  --primary-color: #5bb462;
  --primary-dark: #4f9c55;
  --secondary-color: #ffea00;
  --secondary-dark:#e3d002;
  --overlay: rgba(0,0,0,.7);
  --text-color:#787878;
  --text-size: 1rem;
  --text-size-mobile: 0.5rem;
}

body {

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.button {
  padding: 17px 28px;
  font-size: 1.2rem;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #5bb462;
  background-color: var(--primary-color);
  border: 1px solid #5bb462;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  cursor: pointer;
}
.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
width: 100%;
position: absolute;
justify-content: center;
z-index: -2;
left: 0px;

}
@media screen and (max-width:1000px) {
  .app{
    overflow-x: hidden; /* Hide horizontal scrollbar */
  }

}
.nowrap {
  white-space: nowrap;
}
.header {
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    transition: .3s ease-in;
    overflow: hidden;
    z-index: 1;
    
}
#header {
    background-color:rgba(2, 143, 143, 0.9);
}

.header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 1rem;
    z-index: 1;
}

.header .nav-menu a {
    color: #ffffff;
}

.header .navbar img {
    width: 250px;
    height: auto;
    z-index: 1;
}

.header .nav-menu {
    display: flex;
}

.header .navbar Link {
    padding: 1rem;
    font-weight: 500;
}
.Link:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid var(--secondary-color);
}

.hamburger {
    display: none;
}
.active {
    font-weight: bold;
    padding-bottom: -5px;
    border-bottom: 5px solid var(--secondary-color);
  }

@media screen and (max-width:940px) {
    .header {
        max-width: 100%;
        background-color: rgba(0,0,0,.9);
        color: #000;
    }

    .header .navbar {
        max-width: 100%;
    }

    .hamburger {
        display: block;
    }

    .nav-menu {
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgba(0,0,0,.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }

    Link {
        margin: 1.5rem 0;
    }
}



.about {
    background-color: rgb(255, 255, 255);
}

.about .container {
    width: 1240px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.about .about-header {
    font-size: 3rem;
    text-align: center;
    margin-bottom: 20px;
    
}

.about .container .about-img{
    border-radius: 10px;
    position: relative;
    justify-content: center;
    height: 100%;
    max-height: 800px;
    max-width: 600px;
    margin: auto;
}

.about .container .about_card_list{
    height: 100%;
    width: 100%;
    display: flex;
   flex-wrap: wrap;
   flex-direction: column;
    margin: auto;
    margin-left: 10px;
}
@media screen and (max-width:940px) {
    .about .container {
        grid-template-columns: 1fr;
        width: 100%;
       
    }

    .about .container .about_card_list{
        padding-top: 10px;
        height: 100%;
        width: 80%;
        display: flex;
      flex-wrap: wrap;
      flex-direction: column;
        margin: auto;
    }
    .about .container .about-img{
        border-radius: 10px;
        position: relative;
        justify-content: center;
        max-height: 100%;
        max-width: 80%;
        margin: auto;
    }
}


.about_card{
  border: 1px solid #eee;
  border-radius: 8px;
  box-shadow: 0 3px 10px rgb(0,0,0,.2);
  padding: 1rem;
  width: 90%;
  height: 80px;
  display:flex;
  margin: auto;
 
}
.about_card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    box-shadow: 0 8px 15px rgb(0,0,0,.2);
  }
.about_card .about_card_icon{
    width:30%;
    height: 60%;
    font-size: 100px;
    position: relative;
    top: -15px;
    left: -10px;
}
.about_card .about_card__body{
    text-align: left;
    width: 90%;
    position: relative;
    left: 0px;
    
}
.about_card .about_card__body .about_card__title{
    text-align: left;
    font-size:larger;
}


@media screen and (max-width:940px) {
  .about_card{
    width: 90%;
    height: 50px;
  }  
  .about_card .about_card__body .about_card__description{
    font-size: var(--text-size-mobile);
}
}

