.footer {
    flex-shrink: 0;
    text-align: center;
    color: white;
    background-color:rgba(2, 143, 143, 0.9);
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    }

.footer_container{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 10px;

  
}

.footer_content{
  display: flex;
  flex-direction: column;
  text-align: left;

  align-items: center;
  justify-content: center;
  width: 200px;
}

.footer_link:hover { 
    color: var(--secondary-dark);
    text-align: center;
    font-weight: bold;
    text-decoration: underline;
}

.footer-copyright{
    display: flex;
    flex-direction: row;
}
.footer-icon{
    padding-top: 17px;
    margin-right: 5px;
}
@media (max-width: 940px) {
    .footer{
        width: 100%;
    }
    .footer_container{
       display: flex;
       flex-direction: column;
   

    }
    .footer_content{
        width: 80%;
    }
    
  }
.hero {
    background-color: var(--overlay);
    color: #fff;
    height: 100vh;
    width: 120%;
    overflow-x: hidden; /* Hide horizontal scrollbar */
    z-index: 2;
    top:-100px;
  
}

.hero:before {
    content: '';
    background:url(https://tshoko.github.io/csb-9vhwxo/static/media/hero.03dc027d.jpg)no-repeat center center/cover;
    height: 100vh;
    width: 120%;
    position:absolute;
    top: 0px;
    left: 0px;
    z-index:-2;

}

.hero .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

.hero .content p:first-child {
    font-style: italic;
    font-size: .9rem;
}

.hero .content p:nth-child(2) {
    font-size: 1.9rem;
}

.hero .content p:nth-child(3) {
    font-size: 5rem;
    font-weight: 700;
}


.hero .content p:nth-child(4) {
    font-size: 2.2rem;
    font-style: italic;
}

.hero .button {
    margin: 1rem 0;
    width: 150px;
    height: 35px;
    background-color: #1EB2A6;
    color: #fff;
    text-align: center;
}

.hero .button:hover {
    background-color: var(--secondary-dark);
    border: 1px solid var(--secondary-dark);
}

@media screen and (max-width:940px) {
    .hero .content p:nth-child(3) {
        font-size: 2rem;
        width: 100%;
    }

    .hero .content p:nth-child(4) {
        font-size: 1.75rem;
        width: 100%;
    }


}







.pricing_container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.pricing_card_container {
  position: relative;
  display: flex;
  flex-direction: row;
}
.plan-price {
  font-size: 4rem;
  font-style: italic;
  font-stretch: ultra-condensed;
}
.pricing_card_image {
  border-radius: 50px;
  margin-top: 2rem;
  width: 40%;
  text-align: center;
  color: white;
}
.card_text,
.plan-title,
.plan-type {
  color: --text-color;
}
.plan-title {
  text-align: center;
}
.card_text,
.plan-type {
  padding-left: 10px;
}
.plan_card {
  border: 1px solid #eee;
  border-radius: 20px;
  box-shadow: 0 3px 10px rgb(0, 0, 0, 0.2);
  content: "";
  width: 300px;
  padding: 1rem;
  margin-left: 100px;
  opacity: 1.6 black;
  font-weight: bold;
}
.plan_card:hover {
  border: 1px solid rgb(0, 0, 0);
  box-shadow: 0 15px 25px rgb(0, 0, 0, 0.2);
  opacity: 0.7;
  -webkit-transform: scale(1.0057);
          transform: scale(1.0057);
  transition: 60ms;
}

@media only screen and (max-width: 940px) {
  .pricing_card_container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
  }
  .plan_card {
    width: 90%;
    margin: auto;
    margin-bottom: 20px;
  }
  .plan-price {
    font-size: 2rem;
    font-style: italic;
    font-stretch: ultra-condensed;
  }
}

.team {
    width: 100%;
}

.team .container {
