    text-align: center;
}
.team h3 {
    text-align: center;
}

.team .container .content {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 20px;
    margin: 4rem 0;
}

.team .card {
    border: 1px solid #eee;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgb(0,0,0,.2);
    max-width: 50;
}

.team img {
    border-radius: 50px 50px 0px 0px;
    margin-top: -2rem;
    width: 100%;
}


.team .card p {
    color:var(--text-color);
    font-size: 1.1rem;
    margin: 12px;
    align-items: center;
}

.team .card span {
    color: var(--primary-color);
    font-size: 1rem;
    font-weight: 700;
    align-items: center;
}


@media screen and (max-width: 900px) {
    .team .container {
